@import url('https://fonts.googleapis.com/css2?family=Sora&display=swap');

$darkAccent: #052727;
$md-border-radius: 5px;
$breakpoint-mobile: 640px;

.App {
  font-family: 'Sora', sans-serif;
  display: flex;
  flex-direction: row;
  background-color: rgb(160, 166, 241);
  min-height: 100vh;
  @media (max-width: $breakpoint-mobile) {
		display: flex;
    flex-direction: column;
    width: 100%;
    overflow-x: hidden;
	}
  .editor-wrapper {
    color: black;
    width: 50vw;
    padding: 25px;
    @media (max-width: $breakpoint-mobile) {
      min-width: 85vw;
      height: 50vw;
    }
    #editor {
      font-family: 'Sora', sans-serif;
      padding: 5px;
      resize: none;
      width: 100%;
      height: 100%;
      border-radius: $md-border-radius;
    }
  }
  .preview-wrapper {
    color: black;
    width: 50vw;
    padding: 25px;
    
    @media (max-width: $breakpoint-mobile) {
      min-width: 93%;
      padding: 10px;
      margin-left: 1vw;
    }
  }
}

.markdown {
  padding: 0 1rem;
  
  blockquote {
    border-left: 3px solid $darkAccent;
    color: $darkAccent;
    padding-left: 5px;
    margin-left: 25px;
  }

  > p > code {
    padding: 3px;
  }

  code {
    background: white;
    font-size: 0.875rem;
    font-weight: bold;
    border-radius: $md-border-radius;
  }

  pre {
    display: block;
    overflow: auto;
    background: white;
    padding: 5px;
    line-height: 1.2;
    border-radius: $md-border-radius;
  }

  h1 {
    border-bottom: 2px solid $darkAccent;
  }

  h2 {
    border-bottom: 1px solid $darkAccent;
  }

  table {
    border-collapse: collapse;
  }

  td,
  th {
    border: 2px solid $darkAccent;
    padding-left: 5px;
    padding-right: 5px;
  }

  img {
    padding-left: 20px;
    display: block;
    width: 10%;
    height: 4vw;
    @media (max-width: $breakpoint-mobile) {
      height: 8vw;
    }
  }
  
}
